import Order_item from '../components/order_item/Order_item'
import axios from 'axios'
axios.defaults.withCredentials=true;
import React,{Component} from 'react'
import Link from 'next/link'

class Order extends Component{
    constructor(){
        super()
        this.state={data:[]}
        this.set=this.set.bind(this)
        axios.get('http://localhost:3002/cookie').then(response=>{
            axios.get(`http://localhost:3002/cart/get_placed_order/${response.data}`)
            .then(response=>{
                this.set(response.data)
            })
        })
    }
    set(data){
        this.setState({data:data})
    }

   render(){
       console.log(this.state.data)
       const boxe=this.state.data.map(item=>{
           return(<Order_item name={item.orders.name} price={item.orders.price} url={item.orders.url} category={item.orders.category}/>)
       })
       console.log(boxe)
    return(
        <div id='main'>{boxe}
        <style jsx>
            {`
            #main{
                border:1px solid black;
                width:100%;
                background-color: rgba(230,0,0,0.2);
            }

            `}
        </style>
        </div>
    )
}
}

export default Order;


